html, body, #root, #root > div {
  height: 100%;
}

body {
  font-family: 'Crimson Text', serif !important;
}

#root > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.welcomeScreen {
  text-align: center;
}


.finalScreen {
  text-align: center;
}

.hiddenImages {
  position: absolute;
  top: -9999px;
  left: -9999px;
  width: 100px;
}

.imgWithBounceAnimation {
  width: 100%;
  max-width: 800px;
  display: block;
  margin: 0 auto;
  animation: bounce .7s infinite alternate;
}

.imgWithBorder {
  width: 90% !important;
  max-width: 800px;
  display: block;
  margin: 0 auto;
  border: 7px solid #dedcdc;
}

.imgWithUpDownAnimation {
  width: 100%;
  max-width: 800px;
  display: block;
  margin: 0 auto;
  animation: upDown .7s infinite alternate;
  margin-top: -60px;
}

@keyframes bounce {
  to { transform: scale(1.1); }
}

@keyframes upDown {
  to { transform: translateY(5%); }
}

.responsiveText {
  font-size: 20px !important;
}

@media screen and (min-width: 768px) {

  .responsiveText {
    font-size: 32px !important;
  }
}

.button {
  background: transparent;
  border: none;
  width: 300px;
  cursor: pointer;
  display: block;
  margin: 0 auto;
}


.buttonImage {
  width: 100%;  
}